body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fbf1a9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.noselect {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.toast {
  position: fixed;
  z-index: 3;
  width: 16rem;
  left: 50%;
  margin-left: -8rem;
  padding: 1rem;
  background-color: #19a974;
  box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.2);
  text-align: center;
  transition: 0.5s;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
}

.toast:hover {
  cursor: pointer;
  background-color: #137752;
}

.toast.show {
  bottom: 3rem;
  opacity: 1;
  visibility: visible;
}

.add-list {
  color: #444;
}

.add-list:hover {
  color: #222;
}

.list-menu:hover, .list-menu:focus {
	background-color: #e6c300
}

button.bg-green:disabled {
  background-color: #9EEBCF;
  color: #555;
}

button.bg-green:disabled:hover {
  background-color: #9EEBCF;
  cursor: not-allowed;
}

.lists {
	max-height: 75vh;
	overflow-y: auto;
}

.toggle-lists:hover {
	background-color: #e6c300;
}

ul li {
	background-color: #eee;
	transition: 0.2s;
}

ul li:nth-child(odd) {
	background-color: #f9f9f9;
}

ul li.checked {
	background-color: #888;
	color: #fff;
}

ul li:hover, ul li:focus {
	background-color: #bbb;
}

.actions-content {
	position: absolute;
	right: 0;
	top: 50px;
	z-index: 2;
	background-color: #eee;
}

.actions-content>button {
	display: block;
	background-color: transparent;
	padding: 12px 16px;
	border: none;
	text-align: left;
}

.actions-content>button:hover, .actions-content>button:focus {
	background-color: #ddd;
}

.actions-dropdown {
  min-width: 48px;
	padding-top: 10px;
}

.check {
  min-width: 48px;
}

.item-name {
	word-break: break-all;
	word-break: break-word;
}

.copy-text {
	position: absolute;
	left: -9999px;
}

.list-title {
	overflow-wrap: break-word;
}

